  }
@media screen and (min-width: 750px) {
    
    .navbar{
        display: none;
    }
    

    
    
    
  }
  @media screen and (max-width: 750px) {
    
    .noncollapse-navbar{
        display: none;
    }
    .sidebar{
        display: none;
    }
    .home-section{
        width:100%
    }
    .top{
        width:100%
    }
    .catalog{
        width:100%
    }
    .transactions{
        width:100%
    }
    .import{
        width:100%
    }
    .export{
        width:100%
    }
    .customers{
        width:100%
    }
.itemprofile{
    width: 100%;
}
.customerprofile{
    width: 100%;
}

    
    
  }
  
ul{
    display: flex;
    list-style: none;
    justify-content: space-between;
    align-items: center;
}
.transactions,.customerprofile,.itemprofile {
    overflow-x: auto;
}
.sidebar div,.collapsible-sidebar div{
    overflow-y: auto;
    overflow-x: auto;
}